.App {
  font-family: 'Verdana';
  font-size: 14px;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

.Content {
  position: relative;
  padding: 10px 15px;
  margin: 20px auto;
  cursor: pointer;
}

.Form {
  margin: 0 auto;
  width: 300px;
}

.SubmitButton {
  margin: -5px 5px;
}

.DeleteIcon {
  position: relative;
  top: .25rem;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  text-align: center;
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
